___CSS_LOADER_EXPORT___.push([module.id, `.hello, .update {
    display: flex;
    flex-direction: column;
}

.update {
    align-items: center;
}

button {
    width: 100%;
    margin: 5px 0;
    padding: 10px 20px;
    font-size: 16px;
    background-color: #76a5af;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

button:hover {
    background-color: #45818e;
    /*      */
}

.user-name {
    border: 1px solid #45818e;
    color: #45818e;
    padding: 5px 0;
    border-radius: 5px;
    font-size: 20px;
    font-weight: bold;
    text-align: center;
}

.form-container {
    display: flex;
    flex-direction: column;
}

.form-input {
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-sizing: border-box;
}

.form-input:focus {
    outline: none;
    border-color: #45818e;
}

.error-message {
    color: red;
    font-size: 14px;
    margin-top: 5px;
    text-align: center;
}

.loader {
    margin-top: 10px;
    border: 4px solid rgba(0, 0, 0, 0.1);
    border-left-color: #45818e;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,sBAAsB;AAC1B;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,WAAW;IACX,aAAa;IACb,kBAAkB;IAClB,eAAe;IACf,yBAAyB;IACzB,WAAW;IACX,YAAY;IACZ,kBAAkB;IAClB,eAAe;IACf,sCAAsC;AAC1C;;AAEA;IACI,yBAAyB;IACzB,mCAAmC;AACvC;;AAEA;IACI,yBAAyB;IACzB,cAAc;IACd,cAAc;IACd,kBAAkB;IAClB,eAAe;IACf,iBAAiB;IACjB,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,sBAAsB;AAC1B;;AAEA;IACI,aAAa;IACb,eAAe;IACf,sBAAsB;IACtB,kBAAkB;IAClB,sBAAsB;AAC1B;;AAEA;IACI,aAAa;IACb,qBAAqB;AACzB;;AAEA;IACI,UAAU;IACV,eAAe;IACf,eAAe;IACf,kBAAkB;AACtB;;AAEA;IACI,gBAAgB;IAChB,oCAAoC;IACpC,0BAA0B;IAC1B,kBAAkB;IAClB,WAAW;IACX,YAAY;IACZ,kCAAkC;AACtC;;AAEA;IACI;QACI,uBAAuB;IAC3B;;IAEA;QACI,yBAAyB;IAC7B;AACJ","sourcesContent":[".hello, .update {\r\n    display: flex;\r\n    flex-direction: column;\r\n}\r\n\r\n.update {\r\n    align-items: center;\r\n}\r\n\r\nbutton {\r\n    width: 100%;\r\n    margin: 5px 0;\r\n    padding: 10px 20px;\r\n    font-size: 16px;\r\n    background-color: #76a5af;\r\n    color: #fff;\r\n    border: none;\r\n    border-radius: 5px;\r\n    cursor: pointer;\r\n    transition: background-color 0.3s ease;\r\n}\r\n\r\nbutton:hover {\r\n    background-color: #45818e;\r\n    /*      */\r\n}\r\n\r\n.user-name {\r\n    border: 1px solid #45818e;\r\n    color: #45818e;\r\n    padding: 5px 0;\r\n    border-radius: 5px;\r\n    font-size: 20px;\r\n    font-weight: bold;\r\n    text-align: center;\r\n}\r\n\r\n.form-container {\r\n    display: flex;\r\n    flex-direction: column;\r\n}\r\n\r\n.form-input {\r\n    padding: 10px;\r\n    font-size: 16px;\r\n    border: 1px solid #ccc;\r\n    border-radius: 5px;\r\n    box-sizing: border-box;\r\n}\r\n\r\n.form-input:focus {\r\n    outline: none;\r\n    border-color: #45818e;\r\n}\r\n\r\n.error-message {\r\n    color: red;\r\n    font-size: 14px;\r\n    margin-top: 5px;\r\n    text-align: center;\r\n}\r\n\r\n.loader {\r\n    margin-top: 10px;\r\n    border: 4px solid rgba(0, 0, 0, 0.1);\r\